@import url('https://fonts.googleapis.com/css?family=Montserrat:600&display=swap');

@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.14.0/css/all.min.css');




:root {
  --bg: #dcdde1;     
  --color-icon: #535c68;  
  --social-icon1: #e4405f;
  --social-icon2: #3b5999;
  --social-icon3: #e4405f;
  --social-icon4: #cd201f;
  --social-icon5: #0077B5;  
}

html {
  font-family: Georgia, 'Times New Roman', Times, serif;
  line-height: 1.15;
  text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body{
  margin: 0;
  padding: 0;
  background: #030909;
  background-size: cover;
  background-position: center;
  box-sizing: border-box;
}
header {
  margin-top: 36px;
    color: white;
    font-weight: bolder;
    text-align: center;
    font-size: 4rem;

}
h4 {
  text-align: center;
    color: gray;
    font-style: italic;
    margin-top: 0px;
}

.main-card{
  margin: 100px auto;
  background: #214b43;
  border-radius: 9px;
  box-shadow: #202020;
  width: 800px;
  height: 100%;
  box-sizing: border-box;
  padding: 10px;
  word-wrap: break-word;
}

.coin-card{
margin: 20px 20px 0px 20px;
padding: 10px;
display: flex;
justify-content: space-between;
font-size: 18px;
letter-spacing: 2px;

}

.total-balance, .stake-balance{
  font-size: 18px;
  padding: 10px;
  color: whitesmoke;
  text-align: center;

}
.figure {
color: #2abf29;
}

.stake-tokens{
  padding: 10px 30px 70px 30px;
  color: white;
  border-radius: 7px;
border: 0.2px solid #fff;
  margin: 20px 30px 20px 30px;
  letter-spacing: 2px;
}
#info {
  color: white;
}
.account-card{
  display: flex;
  justify-content: space-between;
  padding: 10px;
  align-content: space-between;
  font-size: 18px;
letter-spacing: 2px;
}

.form-group{
padding: 30px 20px 50px 20px;
border-radius: 7px;
border: 0.2px solid #fff;
box-sizing: border-box;
font-size: 18px;
letter-spacing: 2px;
}

.stake-input{
padding: 10px;
border-radius: 7px;
border: 0.2px solid #fff;
width: 100%;
background: transparent;
color: white;
box-sizing: border-box;
margin-bottom: 30px;
}

.cta{
  display: flex;
  justify-content: center;
  width:100%;
}

.btn-submit, .btn-withdraw, .btn-remove {
margin-left: 10px;
color: white;
padding: 10px 40px;
text-align: center;
border-radius: 7px;
margin-bottom: 20px;
width: 100%;
cursor:pointer;
}

.btn-submit{
  background: #2abf29;
  font-size: 18px;
  letter-spacing: 2px;
}

.btn-connect{
  background: #2abf29;
  font-size: 18px;
  letter-spacing: 2px;
  color: white;
  cursor:pointer;
}

.btn-withdraw{
  background: #e2c441;
  font-size: 18px;
  letter-spacing: 2px;
}

.btn-remove{
  background: red;
  font-size: 18px;
  letter-spacing: 2px;
}


@media (max-width: 767px){
  .main-card{
      width: 100%;
      height: 100%;
      box-sizing: border-box;
  }

  .coin-card, .account-card{
      display: flex;
      flex-direction: column;
      
  }

  

  .cta{
      display: flex;
      justify-content: center;
  }
  
  .btn-submit, .btn-withdraw, .btn-remove{
  margin-left: 10px;
  color: white;
  padding: 10px;
  text-align: center;
  border-radius: 7px;
  }
}